.root {
    position: relative;
    left: -120px;
}
.input {
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 12px 20px;
    padding-left: 42px;
    width: 350px;
    border-radius: 10px;
    font-size: 16px;
    &:focus {
        border: 1px solid rgba(0, 0, 0, 0.3);
    }
}
.icon {
    position: absolute;
    width: 22px;
    height: 22px;
    left: 14px;
    top: 12px;
    opacity: 0.3;
}
.clearIcon {
    position: absolute;
    top: 12px;
    right: 15px;
    height: 22px;
    width: 22px;
    opacity: 0.3;
    cursor: pointer;

    &:hover {
        opacity: 0.8;
    }
}
@media (max-width: 1320px) {
    .root {
        left: 0;
    }
}

@media (max-width: 480px) {
    .input {
        width: 300px;
    }
}
